.container {
        padding: 0 20px;
        margin: 0 auto;
        min-width: 320px;

        @media screen and (min-width: 320px) {
            width: 320px;
        }

        @media screen and (min-width: 768px) {
            width: 768px;
            padding: 0 32px;
        }

        @media screen and (min-width: 1024px) {
            width: 1024px;
        }
    }